body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body, html {
  background: #f6f8fa;
}

.App {
  width: 100%;
  max-width: 800px;
  margin: auto;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: stretch;
          align-items: stretch;

  box-sizing: border-box;
  padding: 30px;
}

.searchBar {
  border: 0;
  border-bottom: 2px solid #b5b5b5;
  padding-bottom: 15px;
  background: transparent;

  transition: border-bottom-color .2s ease-in-out;

  font-size: 2em;
}

.searchBar:focus {
  border-bottom-color: #767676;
  outline: 0;
}

.funds {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: stretch;
          align-items: stretch;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.fund {
  width: 32%;
  margin-right: 2%;

  background: #fff;

  box-sizing: border-box;
  padding: 1rem;
  margin-top: 20px;

  transition: all .2s ease-in-out;

  cursor: pointer;
}

.fund:nth-child(3n+3) {
  margin-right: 0;
}

.fund h1 {
  font-size: 1em;
}

.fund:hover {
  -webkit-transform: scale(1.025);
          transform: scale(1.025);
  box-shadow: 0 0 14px rgba(0,0,0,.12);
}

.tags {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: stretch;
          align-items: stretch;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.tag {
  margin-top: 20px;
  margin-right: 2%;
  width: 32%;
  background: #fff;
  text-align: center;

  transition: all .2s ease-in-out;

  cursor: pointer;
}

.tag:hover {
  -webkit-transform: scale(1.025);
          transform: scale(1.025);
  box-shadow: 0 0 14px rgba(0,0,0,.12);
}

.tag.selected {
  background: #008bba;
  color: #fff  ;
}

.tag:nth-child(3n+3) {
  margin-right: 0;
}

